body{
  color: white;
  background-color: black;
  font-family: Helvetica,  Arial, sans-serif;
}
  .container{
    width: 96%;
    padding: 2%;
    display: flexbox;
  }
  .title{
    text-align: center;
    padding-bottom: 3%;
    border-bottom: 2px solid #eaeaea;
  }
  .categories{
    width: 100%;
  }
  input{
    width: 90%;
    color: #333;
    font-size: 1.3rem;
    padding: 2% 5% 2% 5%;
  }
  .images{
    width: 100%;
  }
    .images__item{
      width: 96%;
      padding: 2%;
      text-align: center;
      border-radius: 5px;
      margin-bottom: 3rem;
      background-color: #333;
    }
    .image__title{
      letter-spacing: .5px;
    }
    .image__source{
      max-width: 100%
    }
